"use client";

import { useState } from "react";
import { fetchChatResponse } from "@/utils/api";
import Message from "./Message";

export default function ChatBox() {
  const [messages, setMessages] = useState<
    { text: string; type: "user" | "bot" }[]
  >([]);
  const [input, setInput] = useState("");

  const sendMessage = async () => {
    if (!input.trim()) return;

    setMessages((prev) => [...prev, { text: input, type: "user" }]);
    setInput("");

    const response = await fetchChatResponse(input);
    setMessages((prev) => [...prev, { text: response, type: "bot" }]);
  };

  return (
    <div className="flex flex-col max-w-md mx-auto p-4">
      <div className="h-80 overflow-y-auto border p-2 mb-2">
        {messages.map((msg, index) => (
          <Message key={index} text={msg.text} type={msg.type} />
        ))}
      </div>
      <div className="flex">
        <input
          className="border p-2 flex-1"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
        />
        <button
          className="ml-2 bg-blue-500 text-white p-2"
          onClick={sendMessage}
        >
          Send
        </button>
      </div>
    </div>
  );
}
